import React, { useEffect } from 'react';
import './App.css';
import myImage from './chart.png';
import { showConnect } from '@stacks/connect';
import { openSTXTransfer } from '@stacks/connect';
import { StacksTestnet } from '@stacks/network';
import { AnchorMode, PostConditionMode } from '@stacks/transactions';
import { userSession } from './userSession';

function App() {
  useEffect(() => {
    document.title = "Stacks Savings";
  }, []);
  const handleClick = () => {
	  showConnect({
		  userSession, // `userSession` from previous step, to access storage
		  appDetails: {
		    name: "Stacks Savings",
		    icon: myImage,
		  },
		  onFinish: () => {
			  sendPay();
		  },
		  onCancel: () => {
		    console.log('oops'); // WHEN user cancels/closes pop-up
		  },
		});
  };

  const sendPay = () => {
	  openSTXTransfer({
	  network: new StacksTestnet(), // which network to use; use `new StacksMainnet()` for mainnet
	  anchorMode: AnchorMode.Any, // which type of block the tx should be mined in

	  recipient: 'ST1M27FTB4ZP2Q58V6BWZFTN0MBGWCVD2QWTJD0FK', // which address we are sending to
	  amount: 10000, // tokens, denominated in micro-STX
	  memo: 'Stacks Savings Deposit', // optional; a memo to help identify the tx

	  onFinish: response => {
	    // WHEN user confirms pop-up
	    console.log(response.txid); // the response includes the txid of the transaction
	  },
	  onCancel: () => {
	    // WHEN user cancels/closes pop-up
	    console.log('User canceled');
	  },
	});
  };

  return (
    <div className="app">
      <h1>Stacks Savings</h1>
      <img src={myImage} alt="myImage" className="image" />
      <button onClick={handleClick}>Deposit</button>
      <footer>Under Construction, {new Date().getFullYear()}</footer>
    </div>
  );
}

export default App;
